class PixelCanvas {
    constructor(width, height, scaleW, scaleH, id, dom) {
        const canvas = document.createElement("canvas");
        canvas.textContent = "Your browser does not seem to support HTML5 canvas.";
        canvas.setAttribute("id", id);
        canvas.width = width;
        canvas.height = height;
        this.scaleW = scaleW;
        this.scaleH = scaleH;
        dom.appendChild(canvas);
        this.ctx = canvas.getContext("2d");
        CanvasRenderingContext2D.prototype.pFillRect = function (x, y, xd, yd) {
            return this.fillRect(scaleW * x, scaleH * y, scaleW * xd, scaleH * yd);
        };
        CanvasRenderingContext2D.prototype.pClearRect = function (x, y, xd, yd) {
            return this.clearRect(scaleW * x, scaleH * y, scaleW * xd, scaleH * yd);
        };
        CanvasRenderingContext2D.prototype.pMoveTo = function (x, y) {
            return this.moveTo(scaleW * x, scaleH * y);
        };
        CanvasRenderingContext2D.prototype.pLineTo = function (x, y) {
            return this.lineTo(scaleW * x, scaleH * y);
        };
        CanvasRenderingContext2D.prototype.pGetImageData = function (sx, sy, sw, sh) {
            return this.getImageData(scaleW * sx, scaleH * sy, scaleW * sw, scaleH * sh);
        };
        CanvasRenderingContext2D.prototype.pPutImageData = function (ctxBoardState, x, y) {
            return this.putImageData(ctxBoardState, scaleW * x, scaleH * y);
        };
        CanvasRenderingContext2D.prototype.pTranslate = function (x, y) {
            return this.translate(scaleW * x, scaleH * y);
        };
        CanvasRenderingContext2D.prototype.isFree = function (x, y) {
            if (x < 0 || width / scaleW <= x || height / scaleH <= y) {
                console.log("isFree, Border");
                return false;
            }
            const image = this.getImageData(scaleW * x, scaleH * y, 1, 1).data;
            const colorSum = image.reduce((previous, current) => previous + current, 0);
            console.log("isFree, Not Border: ", { x: x, y: y, free: !colorSum });
            return !colorSum;
        };
    }
}
export default PixelCanvas;
